export default {
	data() {
		return {
			status:"loading",
			searchVal: "",
			date: "按日期筛选",
			starArr: [
				"全部评分",
				"5星",
				"4星",
				"3星",
				"2星",
				"1星"
			],
			star: "按评分筛选"
		}
	},
	methods: {
		search() {

		},

		datePicker: function(e) {
			const that = this;
			that.date = e.detail.value;
		},
		starPicker: function(e) {
			const that = this;
			that.star = that.starArr[e.detail.value];
		},
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/notice/detail",
				query:{
					id:id
				}
			})
		},
		gotoAdd(){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/notice/add"
			})
		},
		clear(index) {
			const that = this;
			if (index == 0) {
				that.date = "按日期筛选";
			} else {
				that.star = "按评分筛选";
			}
		}
	}
}
